import React, { Component } from 'react'
import parse from 'html-react-parser';


export default class showMoreContent extends Component {
    state = { reactMore: false }

    render() {
        const { content } = this.props;
        const { reactMore } = this.state;

        const postcontent = parse(content.replace(/<[^>]+>/g, ''));
        const notMoreText = postcontent.length > 80 ? true : false

        return (

            <>{notMoreText ?
                <><span className={`mb-1 d-inline ${reactMore ? 'trimPostContent2 ' : 'trimPostContent '}`} id={`gridpostContent${0}`}>
                    {postcontent && String(postcontent) ? parse(String(postcontent)) : postcontent}
                </span>
                    <span style={!reactMore ? {display:'inline-flex', cursor:"pointer", padding: "0px"} : {display:'inline-flex', cursor:"pointer", padding: "0px"}} className="readMore_btn ms-1" onClick={(e) => {e.stopPropagation(); this.setState({ reactMore: !reactMore })}}><b> {reactMore ? 'Read Less' : 'Read More'}</b></span>
                </>
                :
                <p id={`gridpostContent${0}`}>{postcontent && String(postcontent) ? parse(String(postcontent)) : postcontent}</p>
            }
            </>

        )
    }
}