import React from "react";
import { baseUrl } from "../../../../../utils";

const GetData = () => {
  return (
    <div className="placeholder-image py-10 d-flex flex-column justify-content-center">
      <div className="mb-3 text-center">
        <img
          className="img-fluid graph-bg"
          src={baseUrl(`dashboard/analytic/image-placeholder.svg`)}
          width={"80px"}
          height={"80px"}
        />
      </div>
      <div className="">
        <h5 class="text-gray-dark text-center">No Data Available</h5>
      </div>
    </div>
  );
};

const NoData = (props) => {

  return props.status ? (
    <tr>
      <td colSpan={props.col}>
        <GetData />
      </td>
    </tr>
  ) : (
    <GetData />
  );
};

export default React.memo(NoData);
