import React from "react";
import {
    Button,
    Modal,
    ButtonToolbar,
    Col,
    Container,
    Row
} from "react-bootstrap";


export default class SeeMoreModal extends React.Component {
    render() {
        const { showHide, handlePopup, title, children, size } = this.props;
        return (
            <Modal
                show={showHide}
                onHide={()=>handlePopup(false)}
                backdrop="static"
                keyboard={false}
                aria-labelledby="contained-modal-title-vcenter"
                centered
                size={size}
            >
                <Modal.Header closeButton>
                    <Modal.Title>{title}</Modal.Title>
                </Modal.Header>
                <Modal.Body className="p-2">
                    <div className="more_container">
                        {children}
                    </div>
                </Modal.Body>
            </Modal>
        );
    }
}
