import React, { Component } from 'react';
import { Splide, SplideSlide, SplideTrack } from '@splidejs/react-splide';
import SliderItem from '../SliderItem';
class MultiImagePopup extends Component {
    constructor(props) {
        super(props);
        this.state = {
            isRequesting: false,
            activeSlideIndex: props.showRecentIndexData ? props.showRecentIndexData : 0
        };
    }

    
    onActiveSlide = (prev, next) => {
        this.setState({ activeSlideIndex: next })
    }

    render() {
        const { showRecentPostData, showRecentIndexData } = this.props

        const { activeSlideIndex } = this.state

        const arrowShow = showRecentPostData.length>1

        const settings = {
            type: 'slide',
            rewind: false,
            speed: 1000,
            loop: arrowShow,
            interval: 7000,
            drag: arrowShow,
            perPage: 1,
            gap: 0,
            autoHeight: true,
            autoplay: false,
            fixedHeight: false,
            padding: "0",
            pagination: false,
            arrows: arrowShow,
            start: showRecentIndexData
        };

        return <div className='thumbnail-preview-popup'>
                
            {showRecentPostData && showRecentPostData.length > 0 ? <Splide hasTrack={false} options={settings} onMove={this.onActiveSlide}>
                <SplideTrack>
                    {
                        showRecentPostData.map((assetItem, assetKey) => <SplideSlide key={assetKey}>
                            <SliderItem assetItem={assetItem} assetKey={assetKey} active={assetKey == activeSlideIndex ? true : false} newPicture={assetItem.postFile} />
                        </SplideSlide>)
                    }
                </SplideTrack>
            </Splide> : null}
                
           
        </div>
    }
}
export default MultiImagePopup
