            import React, { PureComponent } from "react";
            import Iframe from "react-iframe";
            import ReactPlayer from "react-player";
            import { POPUP_VIDEO_RENEW } from "../../../../../../actions/moderationActions";
            const videoIframeType = [
                "youtube",
                "youtu.be",
                "vimeo",
                "tumblr",
                "soundcloud",
                "linkedin",
            ];

            const isVideo = (videoLink) => {
            let isVideoStatus = false;
            if (String(videoLink)) {
                videoIframeType.map((item) => {
                if (String(videoLink).includes(item) && !isVideoStatus)
                    isVideoStatus = true;
                });
            }
            return isVideoStatus;
            };
            class PopUpVideo extends PureComponent {
            constructor(props) {
                super(props);
            }

            render() {
                const { data, shorts } = this.props;

                const wallId = localStorage.getItem("wallId");
                const shortsOptions = "autoplay=1&loop=1&controls=0&muted=1";

                return data && Object.keys(data).length > 0 ? (
                isVideo(data.link) && !String(data.mediaUrl).includes("cloud.tagbox") || [3].includes(data.networkId) ? (
                   <ReactPlayer
                        className="tb_video__ w-100 h-100 min-h-350"
                        url={data.mediaUrl}
                        width="100"
                        height="100"
                        data-type="video"
                        data-network={data.networkId}
                        data-link={data.link}
                        data-wall-id={wallId}
                        data-item-id={data?.id}
                        data-load={0}
                        autoPlay={true}
                        playing={true}
                        muted={true}
                        playsinline={true}
                        loop={shorts}
                        controls={!shorts}
                        config={{
                            facebook: {
                            appId: "117029975354072",
                            attributes: {
                                "data-width": "auto",
                            },
                            },
                        }}
                        onError={(e) => {POPUP_VIDEO_RENEW(e, wallId)}}
                    />
                ) : data.networkId == 30 && ["vk"].includes(data.link) ? (
                    <Iframe
                        className="img-fluid"
                        frameBorder="0"
                        allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
                        src={`${data.mediaUrl}${
                            shorts
                            ? `${data.mediaUrl.includes("?") ? "&" : "?"}${shortsOptions}`
                            : ""
                        }`}
                        allowFullScreen="true"
                        style={{ width: "100%!important" }}
                        data-type="video"
                        data-network={data.networkId}
                        data-link={data.link}
                        data-wall-id={wallId}
                        data-item-id={data?.id}
                        data-load={0}
                    ></Iframe>
                ) : window.navigator.userAgent.includes("Safari") &&
                    !window.navigator.userAgent.includes("Chrome") &&
                    data.mediaUrl.includes("instagram") ? (
                    <img
                        loading="lazy"
                        className="img-fluid"
                        data-wall-id={wallId}
                        data-item-id={data?.id}
                        data-type="video"
                        data-network={data.networkId}
                        data-link={data.link}
                        data-load={0}
                        src={data.mediaUrl}
                    />
                ) : (
                    <video
                        className="tb_post_modal_video w-100 h-100 mx-h-470"
                        data-wall-id={wallId}
                        data-item-id={data?.id}
                        muted
                        autoPlay={true}
                        loop={shorts}
                        controls={!shorts}
                        data-type="video"
                        data-network={data.networkId}
                        data-link={data.link}
                        data-load={0}
                        onError={(e) => POPUP_VIDEO_RENEW(e, wallId)}
                        src={data.mediaUrl}
                    ></video>
                )
                ) : null;
            }
            }

            export default PopUpVideo;
