import React, { Suspense, useState } from "react";
import { Splide, SplideSlide, SplideTrack } from "@splidejs/react-splide";
import PostMedia from "./postMedia";
import MultiImagePopup from "../popup/multiImagePopup";
import SeeMoreModal from "./seeMoreModal";
import { Modal } from "react-bootstrap";

const MultiPostMedia = (props) => {

  const [showRecentIndexData, setShowRecentIndexData] = useState();
  const [showRecentPostModal, setShowRecentPostModal] = useState();
  const [showRecentPostData, setShowRecentPostData] = useState();
  

  const updatePopup=(data,index,status)=>{
    setShowRecentPostModal(status);
    setShowRecentPostData(data);
    setShowRecentIndexData(index);
  }

  const recentPostToggle = (data, index) => (event) => updatePopup(data,index,true)
  const showRecentPostCallback = (dataEvent) => updatePopup(dataEvent,null,null)
   
  const settings = {
    type: "slide",
    rewind: false,
    speed: 1000,
    loop: true,
    interval: 7000,
    drag: true,
    perPage: 4,
    gap: 5,
    autoplay: false,
    padding: "0",
    pagination: false,
    arrows: true,
    breakpoints: {
      0: {
          perPage: 1
      },
      579: {
          perPage: 2
      },
      767: {
          perPage: 2,
      },
      1300: {
          perPage: 2
      }
  }
  };

  return (
    <>
      {props.postsData && Object.keys(props.postsData).length > 4 ? (
        <Splide className="recent-post m-0" hasTrack={false} options={settings}>
          <SplideTrack>
            {props.postsData.map((asset, postIndex) => {
              return (
                <SplideSlide key={postIndex}>
                  <div onClick={recentPostToggle([...props.postsData], postIndex)}>
                    <PostMedia
                      multiPost={1}
                      postFile={asset.postFile}
                      postId={asset.postId}
                      className={"img-fluid"}
                    />
                  </div>
                </SplideSlide>
              );
            })}
          </SplideTrack>
        </Splide>
      ) : (
        props.postsData.map((asset, postIndex) => {
          return (
            <div
              className="d-inline-block"
              onClick={recentPostToggle([...props.postsData], postIndex)}
            >
              <PostMedia
                multiPost={1}
                postFile={asset.postFile}
                postId={asset.postId}
                className={"img-fluid"}
              />
            </div>
          );
        })
      )}
      

   {showRecentPostData ? (


      <Modal className="recentPost-modal" size={'lg'} show={showRecentPostModal} onHide={()=>setShowRecentPostModal(false)} backdrop="static" keyboard={false} aria-labelledby="contained-modal-title-vcenter" centered>
        <Modal.Header className="p-0 border-0" closeButton></Modal.Header>
        <Modal.Body className="p-0">
            <div className="more_container">
            <Suspense fallback={<></>}>
                  <MultiImagePopup
                    showRecentPostData={showRecentPostData}
                    showRecentIndexData={showRecentIndexData}
                  />
                </Suspense>
            </div>
        </Modal.Body>
      </Modal>
      ) : null}
    </>
  );
};

export default React.memo(MultiPostMedia);
