import { Button, CardHeader, CardTitle } from "react-bootstrap";
import { baseUrl } from "../../../../../utils";

const SectionHeading = (props) => {
  return (
    <CardHeader className={`${props.section==1 ? 'p-0 border-0' : props.section==2 ? 'p-5' : 'px-5 pt-5 border-0 pb-0'}`}>
      <CardTitle className="mb-0 d-flex align-items-center justify-content-between">
        <h6 class="card-label font-weight-bolder m-0 ">
         { props.icon ? <img src={baseUrl(`dashboard/analytic/${props.icon}.svg`)}  height={"20px"} width={"20px"} /> : null }  {props.title}</h6>
         {props.total > 5 ? (
          <Button
            variant="link"
            className="p-0"
            onClick={() => props.handlePopup(true)}
          >
            See More
          </Button>
        ) : null}
      </CardTitle>
    </CardHeader>
  );
};

export default SectionHeading;
